.menu-header {
  margin: 0;
  padding: 1rem 0.5rem;
  height: 4rem;
  width: 100%;
  display: flex;
  justify-content: center;
  background-color: #1da02b;
  transition: width 0.2s, height 0.2s;
  z-index: 10;
}
.menu-header.collapsed {
  height: 4rem;
  width: 80px;
  justify-content: center;
  align-items: center;
}
.menu-header.collapsed span {
  display: none;
}
.menu-header img {
  width: auto;
  height: 32px;
  transition: width 0.2s, height 0.2s;
}
.menu-header .menu-head-title {
  color: #DFE4ED;
  word-break: keep-all;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
  font-size: 22px;
  font-weight: bold;
  margin: 0;
}
.ant-menu-dark .ant-menu-inline.ant-menu-sub {
  background-color: #2b2b2b !important;
}
.ant-menu-dark .ant-menu-submenu-title {
  overflow: hidden;
  word-break: keep-all;
}
