.reportModal :global .box {
  width: 96%;
  margin: 0 auto;
}
.reportModal :global .title {
  width: 100%;
  text-align: center;
  font-size: 26px;
  line-height: 70px;
}
.reportModal :global .titleH3 {
  font-size: 16px;
  font-weight: 700;
  height: 50px;
  line-height: 50px;
}
.reportModal :global .box p {
  line-height: 200%;
  font-size: 14px;
}
.reportModal :global .tableBox01 {
  border: 1px solid #000;
  width: 100%;
  text-align: center;
}
.reportModal :global .tableBox01 td {
  height: 40px;
  line-height: 40px;
  border: 1px solid #000;
  text-align: center;
}
.reportModal :global .tableBox01 .odd {
  border-right: 0;
  width: 100px;
}
.reportModal :global .tableBox01 .even {
  border-left: 0;
  width: 250px;
}
.reportModal :global .blue {
  color: #00f;
}
.reportModal :global .tableBox {
  padding-left: 2em;
  border: 1px solid #000;
  width: 100%;
}
.reportModal :global .tableBox td {
  height: 40px;
  line-height: 40px;
  padding-right: 15px;
  border: 1px solid #000;
  width: 350px;
  text-align: left;
  text-indent: 1em;
}
.reportModal :global .listTalbe {
  border: 1px solid #000;
  text-align: center;
}
.reportModal :global .listHead {
  background-color: #ffc000;
  height: 30px;
  line-height: 30px;
  border: 1px solid #000;
}
.reportModal :global .listHead td {
  border: 1px solid #000;
}
.reportModal :global .report_bottom {
  line-height: 40px;
  font-size: 16px;
  font-weight: bold;
}
.reportModal :global .listTr_a td {
  width: auto;
  border: 1px solid #000;
  text-align: center;
}
.footer {
  display: flex;
  justify-content: space-around;
}
