.tableListForm :global .ant-form-item {
  margin-bottom: 24px;
  margin-right: 0;
  display: flex;
}
.tableListForm :global .ant-form-item > .ant-form-item-label {
  width: auto;
  line-height: 32px;
  padding-right: 8px;
}
.tableListForm :global .ant-form-item .ant-form-item-control {
  line-height: 32px;
}
.tableListForm :global .ant-form-item-control-wrapper {
  flex: 1;
}
.tableListForm .submitButtons {
  display: block;
  white-space: nowrap;
  margin-bottom: 24px;
}
.tableListForm :global .ant-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
