.login-page {
  width: 100vw;
  height: 100vh;
  color: #777;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.login-page .login-bg {
  background: url(./login-bg.jpg) no-repeat 50% 40% / cover;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.login-page .login-wrapper {
  height: 100%;
  width: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-page .login-wrapper .login-box {
  width: 420px;
  background-color: white;
  padding: 2rem 1rem;
  box-shadow: 5px 5px 25px 0px rgba(46, 61, 73, 0.1);
  border-radius: 3px;
}
.login-page .login-wrapper .login-box .login-header {
  text-align: center;
  margin-bottom: 40px;
}
.login-page .login-wrapper .login-box .login-header a {
  display: flex;
  align-items: center;
  justify-content: center;
}
.login-page .login-wrapper .login-box .login-header img {
  margin: 0 16px 0 -16px;
  height: 40px;
}
.login-page .login-wrapper .login-box .login-header span {
  font-size: 1.25rem;
  color: rgba(0, 0, 0, 0.85);
  font-family: 'Myriad Pro', 'Helvetica Neue', Arial, Helvetica, sans-serif;
  font-weight: 600;
}
.login-page .login-wrapper .login-box .login-content {
  width: 100%;
  margin: 0 auto;
}
.login-page .login-wrapper .login-box .login-content .login-form-button {
  width: 100%;
}
.login-page .login-wrapper .login-box .login-content .login-form .ant-form-explain {
  margin-top: 0;
  position: relative;
  top: -2px;
}
.login-page .login-wrapper .login-box .login-content .login-form .login-captcha {
  width: 100%;
  height: 100%;
  object-fit: scale-down;
}
.login-page .login-wrapper .login-box .login-content .login-reset-password {
  display: inline-block;
  text-align: center;
}
