.conBar {
  background: #ffffff;
}
.conBar .conflex {
  display: flex;
  justify-content: space-between;
  padding: 30px 50px;
}
.conBar .conflex > div {
  width: 30%;
}
.conBar .radioGroup label {
  display: block;
}
.conBar .conitem {
  border: 1px solid;
  padding: 20px 25px;
  padding-right: 0;
  height: 400px;
}
.conBar .conitem .tit {
  width: 130px;
  display: inline-block;
  vertical-align: top;
}
.conBar .conitem > div:first-child {
  display: flex;
  justify-content: flex-start;
}
.conBar .conitem .outFlex {
  display: block;
}
.conBar .conitem .outFlex textarea {
  width: 200px;
}
