.actionArea {
  width: 100%;
  background-color: white;
  box-shadow: rgba(0, 21, 41, 0.08) 0px 1px 4px;
  min-height: 96px;
  z-index: 10;
  display: flex;
  flex-direction: column;
}
.wrapper {
  width: 100%;
  padding: 1rem 20px;
  background-color: white;
  position: relative;
  flex: 1;
}
.tabArea {
  position: absolute;
  bottom: 0;
  left: 20px;
  display: flex;
}
.tabArea ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}
.tabArea ul li {
  display: inline-block;
  margin: 10px 20px;
  font-size: 14px;
  cursor: pointer;
  position: relative;
}
.tabArea ul li a {
  text-decoration: none;
}
.tabArea ul li.active {
  color: #1dd12f;
}
.tabArea ul li.active:after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: -10%;
  width: 120%;
  height: 2px;
  background-color: #1dd12f;
}
.tabArea ul li:hover {
  color: #1da02b;
}
.active {
  color: #1dd12f;
}
.active:after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: -10%;
  width: 120%;
  height: 2px;
  background-color: #1dd12f;
}
